// import  
import { useState, useCallback, useEffect } from "react";
import { Routes, Route } from "react-router-dom";

// api
import { productApi } from "./api/product";
// component 
import Hd from "./component/layout/Hd";
import Mainvideo from "./component/banner/Videobanner"
import Main_sale from "./pages/Main_sale";
import Mainbanner from "./component/banner/Imgbanner"
import Main_best from "./pages/Main_best";
import Main_service from "./pages/Main_service";
import Form from "./component/main/Form";
import Footer from "./component/layout/Footer";
import Shop_p from "./pages/Shop_p";
import Cart_p from "./pages/Cart_p";
import Login_p from "./pages/Login_p";
import Registration_p from "./pages/Registration_p";

// css
import 'bootstrap/dist/css/bootstrap.min.css';


function App() {
  const [content, setCont] = useState(null); //api  
  const [sharedState, setSharedState] = useState([]); //  pk Array

  //    1   , props slice   
  const fetchDataAndSetState = async () => {
    try {
      const response = await productApi("product");
      setCont([...response.data]);
    } catch (error) {
      console.log(error);
    }
  };

  const updateSharedState = (childliftupvalue) => {
    setSharedState(childliftupvalue);
    //       
    // useEffect  
  };

  useEffect(() => {
    // api   
    fetchDataAndSetState();
  }, []);


  return (
    <div className="App">

      <Routes>
        <Route path="/login" element={
          <>
            <Login_p></Login_p>
          </>
        }>
        </Route>
      </Routes>



      <Routes>
        <Route path="/registration" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <Registration_p></Registration_p>
            <Footer></Footer>
          </>
        }>
        </Route>
      </Routes>


      <Routes>
        <Route path="/sale_p" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <Main_sale
              content={content && content.slice(0, 16)}
              texts={{
                text: "",
                add: null,
              }}
              myclassStyle={{
                divclass: null,
                h4class: "text-center",
                conmargin: "200px"
              }}
            />
            <Footer></Footer>
          </>
        }>
        </Route>

        <Route path="/best_p" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <Main_sale
              content={content && content.slice(0, 16)}
              texts={{
                text: "",
                add: null,
              }}
              myclassStyle={{
                divclass: null,
                h4class: "text-center",
                conmargin: "200px"
              }}
            />
            <Footer></Footer>
          </>
        }></Route>

        <Route path="/shop" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <Shop_p
              conmargin="200px"
              h4class="text-center"
            />
            <Footer></Footer>
          </>
        }></Route>


        <Route path="/cart" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <Cart_p
              sharedState={sharedState}  //  pk          
              setSharedState={setSharedState}
              content={content && content}
              texts={{
                text: "CART",
              }}
              myclassStyle={{
                h4class: "text-center",
                conmargin: "200px"
              }}
            />
            <Footer></Footer>
          </>
        }>
        </Route>


        <Route path="/" element={
          <>
            <Hd sharedState={sharedState}></Hd>
            <div id='slidebanner'>
              <Mainvideo className="position-relative"></Mainvideo>
            </div>
            <Main_sale
              sharedState={sharedState}
              updateSharedState={updateSharedState} //    
              content={content && content.slice(0, 8)} //     //     slice 

              texts={{
                text: "",
                add: <span style={{ color: "000f22" }}></span>
              }}
              myclassStyle={{
                divclass: "d-flex justify-content-between align-items-center",
                h4class: null,
                conmargin: "0px"
              }}
            ></Main_sale>


            <Mainbanner className="position-relative"></Mainbanner>
            <Main_best></Main_best>
            <Main_service></Main_service>
            <Form></Form>
            <Footer></Footer>
          </>
        }></Route>
      </Routes>



    </div >
  );
}

export default App;
