import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom'

function Shop_p({
    conmargin,
    h4class
}) {


    useEffect(() => {
        // Kakao  SDK 
        const script = document.createElement('script');
        script.src = '//dapi.kakao.com/v2/maps/sdk.js?appkey= APP KEY ';
        document.head.appendChild(script);

        script.onload = () => {
            // Kakao.maps     
            window.kakao.maps.load(() => {
                //   div    
                const mapContainer = document.getElementById('map');
                const mapOption = {
                    center: new window.kakao.maps.LatLng(33.450701, 126.570667), //  
                    level: 3 //   
                };

                //  
                const map = new window.kakao.maps.Map(mapContainer, mapOption);
            });
        };

        return () => {
            //     
            document.head.removeChild(script);
        };
    }, []); //      



    return (
        <div style={{ marginTop: conmargin }}>
            <h4 className={h4class}> </h4>
            <div>
                <label htmlFor="avedaMap"></label>
                <select name="avedaMap" id="avedaMap">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                </select>
            </div>
            <div id="map" style={{ width: '100%', height: '350px', border: "1px solid red" }}></div>;

        </div>
    )
}

export default Shop_p
